import React, { useState } from 'react';
import axios from 'axios';
import Select from 'react-select';
import { AlertCircle } from 'lucide-react';

const filterOptions = [
  { value: 'alphabets', label: 'Alphabets' },
  { value: 'numbers', label: 'Numbers' },
  { value: 'highestAlphabet', label: 'Highest Alphabet' }
];

const Form=()=>{
  const [jsonInput, setJsonInput] = useState('');
  const [error, setError] = useState('');
  const [response, setResponse] = useState(null);
  const [selectedFilters, setSelectedFilters] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const validateAndSubmit = async () => {
    try {
      setError('');
      setIsLoading(true);
      
      // Validate JSON
      const parsedJson = JSON.parse(jsonInput);
      
      // Call API
      const result = await axios.post('http://localhost:3000/bfhl', parsedJson);
      setResponse(result.data);
    } catch (err) {
      if (err instanceof SyntaxError) {
        setError('Invalid JSON format');
      } else {
        setError('An error occurred while processing your request');
      }
    } finally {
      setIsLoading(false);
    }
  };

  const renderFilteredResponse = () => {
    if (!response || selectedFilters.length === 0) return null;

    return (
      <div className="space-y-2">
        <h2 className="text-xl font-semibold">Filtered Response</h2>
        {selectedFilters.map((filter) => {
          const value = response[filter.value];
          if (value === undefined) return null;

          return (
            <div key={filter.value} className="py-1">
              <span className="font-medium">{filter.label}: </span>
              {Array.isArray(value) ? value.join(', ') : value}
            </div>
          );
        })}
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-2xl mx-auto">
        <div className="bg-white p-6 rounded-lg shadow-md space-y-6">
          <div className="space-y-2">
            <label htmlFor="jsonInput" className="block text-sm font-medium text-gray-700">
              API Input (JSON)
            </label>
            <textarea
              id="jsonInput"
              rows={4}
              className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500"
              value={jsonInput}
              onChange={(e) => setJsonInput(e.target.value)}
              placeholder='{ "data": ["A","C","z"] }'
            />
          </div>

          {error && (
            <div className="flex items-center gap-2 text-red-600">
              <AlertCircle size={20} />
              <span>{error}</span>
            </div>
          )}

          <button
            onClick={validateAndSubmit}
            disabled={isLoading}
            className="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:opacity-50"
          >
            {isLoading ? 'Processing...' : 'Submit'}
          </button>

          {response && (
            <div className="space-y-4">
              <div className="space-y-2">
                <label className="block text-sm font-medium text-gray-700">
                  Multi Filter
                </label>
                <Select
                  isMulti
                  options={filterOptions}
                  value={selectedFilters}
                  onChange={(selected) => setSelectedFilters(selected || [])}
                  className="basic-multi-select"
                  classNamePrefix="select"
                />
              </div>

              {renderFilteredResponse()}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Form;